.bguest__slideSix{
  display: flex;
  flex-direction: column;
}

.bguest__slideSix-body{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80vh;
    padding-left: 5rem;
    padding-right: 5rem;
}

.bguest__slideSix-body-contents{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 40%;
    height: 100%;
}

.bguest__slideSix-body-contents .animation-tag{
    font-family: "Poppins";
    font-size: 14px;
    font-weight: 400;
    color: var(--color-black);
    padding-top: 1rem;
    font-weight: lighter;
}

.bguest__slideSix-body-contents-thumb-animations{
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    justify-content: space-between;
    margin-top: 1rem;
    border-radius: 8px;
    background-color: white;
}

.bguest__slideSix-body-animation{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 12px;
    width: 50%;
    height: 90%;
    flex-shrink: 0;
    overflow: hidden;
    
}

.bguest__slideSix-body-animation-man-wth-wifi {
    position: relative;
}

.bguest__slideSix-body-animation .wifi-range{
    position: absolute;
    top: -1.5rem;
    left: -1.5rem;
    width: 60px;
    height: 60px;
  
}

.bguest__slideSix-body-animation .router{
    position: absolute;
    top: -5rem;
    right: -1rem;
    width: 150px;
    height: 150px;
}

.bguest__slideSix-body-animation .image1{
    height: 200px;
}

.bguest__slideSix-body-animation .image2{
    height: 500px;
}

